<template>
  <ve-ring
    :data="data"
    height="230px"
    width="100%"
    :extend="extend"
    :settings="chartSettings"
    :legend-visible="legend_Visible"
    :legend-position="legend_Position"
  ></ve-ring>
</template>

<script>
export default {
  data() {
    this.chartSettings = {
      radius: [20, 60],
      offsetY: 100,
      label: {
        normal: {
          show: true,
          position: "outside"
        },
        emphasis: {
          show: true,
          textStyle: {
            fontSize: "12",
            fontWeight: "bold"
          }
        }
      }
    };
    return {};
  },
  props: ["data", "legend_Visible", "legend_Position",'extend']
};
</script>