<template>
  <div class="content">
    <section class="businessContent">
      <section class="top">
        <div class="busine_top">
          <div class="bus_top_block">
            <p>今日营收金额</p>
            <p>6333.455</p>
          </div>
          <div class="icoDiv">
            <van-icon name="balance-list" color="#fbffff" size="25" />
          </div>
          <div class="bus_top_block">
            <p>今日消费人数</p>
            <p>2000</p>
          </div>
        </div>
        <div class="busine_bottom">
          <div class="bus_top_block">
            <p>同比</p>
            <p>
              +10.6%
              <img src="@/assets/rise.png" />
            </p>
          </div>
          <div class="bus_top_block">
            <p>环比</p>
            <p>
              +10.6%
              <img src="../../../assets/rise.png" />
            </p>
          </div>
          <div class="bus_top_block">
            <p>账单总数</p>
            <p>10000笔</p>
          </div>
        </div>
      </section>
      <veLine
        :data="this.getRespone.json"
        :legend="false"
        :settings="line"
        :extend="this.getChartSet.line_extend"
      ></veLine>
      <transition name="sold">
        <div>
          <router-link class="moreLine" to="/businessReport">查看更多</router-link>
        </div>
      </transition>
    </section>

    <section class="receivableContent">
      <div class="receivable">
        <section>
          <div class="title">收款方式</div>
          <veRing
            :data="payAway"
            :legend_Visible="true"
            legend_Position="bottom"
            :extend="percetRing"
          ></veRing>
        </section>
      </div>
      <achieveReport :data="achieveJson" />
      <transition name="sold">
        <div>
          <router-link class="moreLine" to="/payAway">查看更多</router-link>
        </div>
      </transition>
    </section>

    <section class="vipContent">
      <!-- <div class="title">充值消费记录</div> -->
      <veBar
        :chartData="this.getRespone.json"
        :legend_Visible="false"
        :extend='chartsExtends'
      ></veBar>
      <achieveReport :data="vipJson" />
      <transition name="sold">
        <div>
          <router-link class="moreLine" to="/vipReport">查看更多</router-link>
        </div>
      </transition>
    </section>

    <section class="clockContent">
      <veHistogram
        :data="this.getRespone.json"
        :legend_Visible="false"
        :settings="historgramSetting"
      />
      <achieveReport :data="clockJson" />
      <transition name="sold">
        <div>
          <router-link class="moreLine" to="/projectReport">查看更多</router-link>
        </div>
      </transition>
    </section>
    <footerBottom :tabbars="this.getRespone.bossTabbars" />
  </div>
</template>

<script>
import veLine from "@/components/veLine";
import veRing from "@/components/veRing";
import veBar from "@/components/veBar";
import veHistogram from "@/components/veHistogram";
import achieveReport from "@/components/report";
import { log } from "util";

export default {
  data() {
    this.line = {
      yAxisName: ["销售额（万）"]
    };
    this.historgramSetting = {
      yAxisName: ["钟数统计"]
    };
    var self = this;
    self.percetRing = {
      legend: {
        icon: "circle", //  这个字段控制形状
        itemWidth: 10, // 设置宽度
        itemHeight: 10
        // formatter: function(name) {
        //   var index = 0;
        //   var percent = 0;
        //   var total = 0;
        //   self.getPayAway();
        //   self.payAway.rows.forEach(function(value, i) {
        //     if (value.name == name) {
        //       index = i;
        //     }
        //   });
        //   self.payAway.rows.forEach(function(value, i) {
        //     total += parseInt(value.money);
        //   });

        //   if (total === 0) {
        //     return name + "  " + 0 + "%";
        //   } else {
        //     percent = ((self.payAway.rows[index].money / total) * 100).toFixed(
        //       2
        //     );
        //     return name + "  " + percent + "%";
        //   }
        // }
      }
    };
    return {
      chartData: this.getRespone.json,
      achieveJson: [],
      vipJson: [],
      clockJson: [],
      payAway: this.payAway,
      chartsExtends:{
        xAxis: {
          splitLine: {
            show: false // 统计图分割线 x轴
          },
          axisLine: {
            show: false
          }
        },
        yAxis: {
          name:'充值消费记录',
          nameLocation: 'end',
          splitLine: {
            show: false
          },
          position: "left",
          axisLine: {
            show: false
          }
        }
      }
    };
  },
  mounted() {
    this.getAchieve();
    this.getVip();
    this.getClock();
    this.getPayAway();
  },
  methods: {
    getAchieve() {
      var key = this.getRespone.achieveKey;
      var val = this.getRespone.achieveVal;
      var achieve = [];
      key.forEach(key => {
        val.forEach(val => {
          if (key.id === val.id) {
            achieve.push({ key, val });
          }
        });
      });
      this.achieveJson = achieve;
    },
    getVip() {
      var key = this.getRespone.vipKey;
      var val = this.getRespone.achieveVal;
      var achieve = [];
      key.forEach(key => {
        val.forEach(val => {
          if (key.id === val.id) {
            achieve.push({ key, val });
          }
        });
      });
      this.vipJson = achieve;
    },
    getClock() {
      var key = this.getRespone.clockKey;
      var val = this.getRespone.achieveVal;
      var achieve = [];
      key.forEach(key => {
        val.forEach(val => {
          if (key.id === val.id) {
            achieve.push({ key, val });
          }
        });
      });
      this.clockJson = achieve;
    },
    getPayAway() {
      var self = this;
      var rows = self.getRespone.payAway;
      var columns = ["name", "money", "bfb", "num"];
      self.payAway = { columns, rows };
    }
  },
  components: {
    veLine,
    veRing,
    veBar,
    achieveReport,
    veHistogram
  }
};
</script>

<style lang="scss" scoped>
.content {
  @include wh(100%, calc(100% - 50px));
  overflow: scroll;
  .businessContent {
    padding: 0.5rem;
    .top {
      border: 0.02rem solid $Te;
      .busine_top,
      .busine_bottom {
        @include fr(space-around);
        margin: 0.8rem 0.5rem;
        .bus_top_block {
          @include fc(center);
          @include sc(0.85rem, $fColor);
          p {
            font-weight: bold;
            margin-top: 0.3rem;
          }
          p:last-child {
            color: $yellow;
            font-weight: normal;
          }
        }
        .bus_top_block:last-child {
          p:last-child {
            color: $orange;
          }
        }
        .icoDiv {
          @include fc(center);
          @include wh(3rem, 3rem);
          background-color: #49c7f3;
          background-image: linear-gradient(
            180deg,
            #49c7f3 0%,
            #74e5e9 50%,
            #a2ffdf 100%
          );
          border-radius: 5rem;
        }
      }
      .busine_bottom {
        .bus_top_block {
          // border: 0.02rem solid $Te;
          padding: 0.5rem;
          margin: 0rem 0.3rem;
          // flex: 1;
          img {
            @include wh(1rem, 1rem);
            vertical-align: top;
          }
        }
      }
    }
  }
  .receivable {
    @include fr(space-between);
    @include wh(100%, 100%);
    section {
      @include wh(100%, 100%);
    }
    .leftContent {
      @include fc(space-around);
      margin-top: 2.1rem;
      .pay_content {
        @include fr(space-around);
        @include sc(0.8rem, $fColor);
        div {
          text-align: left;
        }
        div:last-child {
          width: 3rem;
        }
        div:nth-child(2) {
          margin: 0.156rem 1rem;
        }
      }
    }
  }
  .vipContent,
  .clockContent,
  .receivableContent {
    padding: 0.5rem;
    background-color: $fc;
  }
  .moreSection {
    background: $fc;
    padding: 0.5rem;
  }
  .title {
    @include sc(0.97rem, $fColor);
    font-weight: bold;
    text-align: left;
    margin: 1rem 1rem 0rem 1rem;
  }
  .line {
    background: $fc;
  }
  .moreLine {
    @include sc(0.8rem, $fColor);
    padding: 0.8rem;
    text-align: center;
    background-color: $Te;
    letter-spacing: 0.2rem;
    display: grid;
  }
}
</style>